 $LightCyan: hsl(193, 38%, 86%) ;
 $NeonGreen: hsl(150, 100%, 66%) ;
 $GrayishBlue: hsl(217, 19%, 38%) ;
 $DarkGrayishBlue: hsl(217, 19%, 24%) ;
 $DarkBlue: hsl(218, 23%, 16%);
 @import url('https://fonts.googleapis.com/css2?family=Manrope:wght@800&display=swap');
 * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    user-select: none;
 }
 body {
    font-family: 'Manrope', sans-serif;
    background: url("../public/images/pexels-pixabay-355465.jpg") center/cover;
    height: 100Vh;
    overflow: hidden;
    color: $LightCyan;
 }
 .card-container {
    min-height: 90vh;
    width: 90%;
    background: $DarkBlue;
    margin: 40px auto 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    .card {
            width: 30%;
            background-color: $DarkGrayishBlue;
            padding: 20px;
            min-height: 220px;
            text-align: center;
            border-radius: 10px;
            position: relative;
            span {
                color: $NeonGreen;
                text-transform: uppercase;
                font-size: 0.6em;
                letter-spacing: 3px;
                
            }
            p {
                margin-top: 20px;
            }
            #divider {
                margin-top: 40px;
                width: 100%;
            }
            i {
                width: 50px;
                height: 50px;
                background-color: $NeonGreen;
                cursor: pointer;
                position: absolute;
                border-radius: 50%;
                left: 50%;
                bottom: -15%;
                transform: translateX(-50%);
                display: flex;
                align-items: center;
                justify-content: center;
                transition: 0.3s ease;
                &:hover {
                -webkit-box-shadow: 0px 2px 15px 9px hsl(150, 100%, 66%);
                    box-shadow: 0px 2px 15px 9px hsl(150, 100%, 66%);
                    img {
                        color: $DarkBlue;
                    }
            }}
         
        }
 }
